import React, { createContext, useContext, useEffect, useState } from 'react';
import {
  getAuth,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signOut,
} from 'firebase/auth';
import {
  getFirestore,
  doc,
  getDoc,
  updateDoc,
} from 'firebase/firestore';

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

const auth = getAuth();
const db = getFirestore(); // Exported for use in other files

// Helper function to deduplicate an array of objects by a given key
const getUniqueItems = (items, key) => {
  if (!Array.isArray(items)) return [];
  return Array.from(new Map(items.map(item => [item[key], item])).values());
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(() => {
    const storedUser = sessionStorage.getItem('user');
    return storedUser ? JSON.parse(storedUser) : null;
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const updateUser = async (currentUser) => {
      if (!currentUser) {
        setUser(null);
        sessionStorage.removeItem('user');
        setLoading(false);
        return;
      }

      try {
        // Retrieve custom claims to check user role
        const idTokenResult = await currentUser.getIdTokenResult();
        const role = idTokenResult.claims.role || 'user';

        // Get the user's document from Firestore
        const userRef = doc(db, 'users', currentUser.uid);
        const userDoc = await getDoc(userRef);
        const firestoreData = userDoc.exists() ? userDoc.data() : {};

        // Deduplicate finishedLessons and finishedWorkbooks arrays
        const rawFinishedLessons = firestoreData.finishedLessons || [];
        const rawFinishedWorkbooks = firestoreData.finishedWorkbooks || [];
        const uniqueFinishedLessons = getUniqueItems(rawFinishedLessons, 'lessonID');
        const uniqueFinishedWorkbooks = getUniqueItems(rawFinishedWorkbooks, 'workbookID');

        // Compute balances for lessons and workbooks separately
        const totalLessonsPurchased = firestoreData.totalLessonsPurchased || 0;
        const remainingLessons = totalLessonsPurchased - uniqueFinishedLessons.length;
        const totalWorkbooksPurchased = firestoreData.totalWorkbooksPurchased || 0;
        const remainingWorkbooks = totalWorkbooksPurchased - uniqueFinishedWorkbooks.length;

        // Determine the global position:
        // - If courseID is empty, mark as "newcomer".
        // - If not a teacher, then if either balance is > 0, mark as "activeStudent"; else, "inactiveStudent".
        let computedPosition = firestoreData.position; // May be manually set (e.g., "teacher")
        if (!firestoreData.courseID) {
          computedPosition = 'newcomer';
        } else if (computedPosition !== 'teacher') {
          computedPosition = (remainingLessons > 0 || remainingWorkbooks > 0)
            ? 'activeStudent'
            : 'inactiveStudent';
        }

        // Update Firestore if the stored position doesn't match the computed one.
        if (firestoreData.position !== computedPosition) {
          await updateDoc(userRef, { position: computedPosition });
        }

        // Determine the next lesson index based on deduplicated finished lessons.
        const nextLessonIndex =
          uniqueFinishedLessons.length < totalLessonsPurchased ? uniqueFinishedLessons.length : null;

        // Build the complete user data object
        const userData = {
          uid: currentUser.uid,
          email: currentUser.email,
          role,
          ...firestoreData,
          finishedLessons: uniqueFinishedLessons,       // Deduplicated list
          finishedWorkbooks: uniqueFinishedWorkbooks,     // Deduplicated list
          totalLessonsPurchased,
          totalWorkbooksPurchased,
          remainingLessons,      // Computed lessons balance
          remainingWorkbooks,    // Computed workbooks balance
          nextLessonIndex,       // Next lesson index for access
          position: computedPosition,  // Global computed position
        };

        // Cache the data in sessionStorage and update state
        sessionStorage.setItem('user', JSON.stringify(userData));
        setUser(userData);
      } catch (error) {
        console.error('Error fetching user data or custom claims:', error);
        setError('Failed to load user data.');
      } finally {
        setLoading(false);
      }
    };

    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      updateUser(currentUser);
    });

    return () => unsubscribe();
  }, []);

  const login = async (email, password) => {
    setLoading(true);
    setError(null);
    try {
      await signInWithEmailAndPassword(auth, email, password);
    } catch (error) {
      console.error('Login error:', error);
      setError('Failed to log in. Please check your credentials and try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  const logout = async () => {
    setLoading(true);
    setError(null);
    try {
      await signOut(auth);
      setUser(null);
      sessionStorage.removeItem('user');
    } catch (error) {
      console.error('Logout error:', error);
      setError('Failed to log out. Please try again.');
      throw error;
    } finally {
      setLoading(false);
    }
  };

  const value = {
    user,
    login,
    logout,
    loading,
    error,
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};

// Export db for use in other parts of your app
export { db };
